import React from 'react'
import Panel from './Panel'
import Slider from '@material-ui/core/Slider'
import Switch from '@material-ui/core/Switch'

export default class Board extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      volume: 0,
      savedVolume: 0,
      mute: false
    }
  }

  handleChange (event, number) {
    this.setState({
      volume: number / 100
    })
  }

  toggleCheck () {
    const currentVolume = this.state.mute ? this.state.savedVolume : 0
    this.setState({
      mute: !this.state.mute,
      savedVolume: this.state.volume,
      volume: currentVolume
    })
  }

  render () {
    const labelVolume = Math.trunc(this.state.volume * 100)
    const muteLabel = !this.state.mute ? 'ON' : 'OFF'
    return (
      <div
        id='board'
        className='board'
      >
        <div id='keyboard-area'>
          <p id='display'>---</p>
          <Panel
            volume={this.state.volume}
          />
        </div>
        <div id='volume-controllers'>
          <p>Volume: {labelVolume}</p>
          <Slider
            className='slider'
            orientation='vertical'
            onChange={this.handleChange.bind(this)}
            value={this.state.volume * 100}
          />
          <Switch onChange={this.toggleCheck.bind(this)} />
          <p>{muteLabel}</p>
        </div>
      </div>
    )
  }
}
