import $ from 'jquery'
import React from 'react'

export default class Pad extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      play: false
    }
  }

  componentDidMount () {
    $('#' + this.props.letter)[0].volume = this.props.volume
  }

  shouldComponentUpdate (nextProps, nextState) {
    return (nextProps.play !== this.props.play || nextProps.volume !== this.props.volume)
  }

  componentDidUpdate () {
    if (this.props.play !== false) {
      this.setState({ play: true }, () => {
        this.handlePlay()
        this.setState({
          play: false
        })
      })
    }
    $('#' + this.props.letter)[0].volume = this.props.volume
  }

  handleClick () {
    this.handlePlay()
  }

  handlePlay () {
    $('#' + this.props.letter)[0].play()
    $('#display').text(this.props.name)
  }

  render () {
    return (
      <div
        id={'pad-' + this.props.letter}
        className='pad drum-pad'
        onClick={this.handleClick.bind(this)}
      >
        <audio
          id={this.props.letter}
          src={this.props.sound}
          className='clip'
        />
        {this.props.letter}
      </div>
    )
  }
}
